import React from "react";
import { Button } from "antd";

//MediaStream
let stream;
/**
 * 
 */
class MediaStreamAPI extends React.Component {
    constructor() {
        super();
    }

    componentDidMount() {
        this.openDevice();
    }

    //
    openDevice = async () => {
        try {
            //
            stream = await navigator.mediaDevices.getUserMedia({
                //
                audio: true,
                //
                video: true
            });
            let video = this.refs['myVideo'];
            video.srcObject = stream;
        } catch (e) {
            console.log(`getUserMedia:` + error);
        }
    }

    //
    btnGetAudioTracks = () => {
        console.log("getAudioTracks");
        //
        console.log(stream.getAudioTracks());
    }

    //Id
    btnGetTrackById = () => {
        console.log("getTrackById");
        console.log(stream.getTrackById(stream.getAudioTracks()[0].id));
    }

    //
    btnRemoveAudioTrack = () => {
        console.log("removeAudioTrack()");
        stream.removeTrack(stream.getAudioTracks()[0]);
    }

    //,
    btnGetTracks = () => {
        console.log("getTracks()");
        console.log(stream.getTracks());
    }

    //
    btnGetVideoTracks = () => {
        console.log("getVideoTracks()");
        console.log(stream.getVideoTracks());
    }

    //
    btnRemoveVideoTrack = () => {
        console.log("removeVideoTrack()");
        stream.removeTrack(stream.getVideoTracks()[0]);
    }

    render() {
        return (
            <div className="container">
                <h1>
                    <span>MediaStreamAPI</span>
                </h1>
                <video className="video" ref="myVideo" autoPlay playsInline></video>
                <Button onClick={this.btnGetTracks} style={{width:'120px'}}></Button>
                <Button onClick={this.btnGetAudioTracks} style={{width:'120px'}}></Button>
                <Button onClick={this.btnGetTrackById} style={{width:'200px'}}>Id</Button>
                <Button onClick={this.btnRemoveAudioTrack} style={{width:'120px'}}></Button>
                <Button onClick={this.btnGetVideoTracks} style={{width:'120px'}}></Button>
                <Button onClick={this.btnRemoveVideoTrack} style={{width:'120px'}}></Button>
            </div>
        );
    }
}
//
export default MediaStreamAPI;
