import React, { Component } from 'react'
import PropTypes from "prop-types";
import VideocamOffIcon from "mdi-react/VideocamOffIcon";
/**
 * 
 */
export default class LocalVideoView extends Component {

    //
    componentDidMount = () => {
        //
        let video = this.refs[this.props.id];
        //stream
        video.srcObject = this.props.stream;
       //MetaData
        video.onloadedmetadata = (e) => {
            video.play();
        };
    }

    render() {
        //
        const small = {
            display:'flex',
            justifyContent: 'center',
            alignItems: 'center',
            //
            position: 'absolute',
            //
            width: '192px',
            //
            height: '108px',
            //
            bottom: '60px',
            //
            right: '10px',
            //
            borderWidth: '2px',
            //
            borderStyle: 'solid',
            //
            borderColor: '#ffffff',
            //
            overflow: 'hidden',
            //
            zIndex: 99,
            //
            borderRadius: '4px',
        };
        //
        const videoMuteIcon = {
            position: 'absolute',
            color:'#fff',
          }

        return (
            <div key={this.props.id}
                //
                style={small}>
                {/* refid  objectFitcover */}
                <video ref={this.props.id} id={this.props.id} 
                autoPlay playsInline muted={true}
                style={{ width: '100%', height: '100%', objectFit: 'cover', }} />
                {
                    //    
                    this.props.muted? <VideocamOffIcon style={videoMuteIcon}/> : null
                }
            </div>
        )
    }
}
//
LocalVideoView.propTypes = {
    //
    stream: PropTypes.any.isRequired,
    //Id
    id: PropTypes.string,
}
