import React from "react";
import { Button } from "antd";

//
let mediaRecorder;
//
let recordedBlobs;
//
let stream;
/**
 * 
 */
class RecordScreen extends React.Component {

  //
  startCaptureScreen = async (e) => {
    try {
      //getDisplayMedia,{video:true}
      stream = await navigator.mediaDevices.getDisplayMedia({
        //
        video: {
          width: 2880, height: 1800
        }
      });

      const video = this.refs['myVideo'];
      //
      const videoTracks = stream.getVideoTracks();
      //
      console.log(`: ${videoTracks[0].label}`);
      window.stream = stream;
      //stream
      video.srcObject = stream;

      this.startRecord();

    } catch (e) {
      console.log('getUserMedia:' + error);
    }
  }

  //
  startRecord = (e) => {
    //,
    stream.addEventListener('inactive', e => {
      console.log('!');
      this.stopRecord(e);
    });

    //
    recordedBlobs = [];
    try {
      //MediaRecorder,
      mediaRecorder = new MediaRecorder(window.stream, { mimeType: 'video/webm' });
    } catch (e) {
      console.error('MediaRecorder:', e);
      return;
    }

    //
    mediaRecorder.onstop = (event) => {
      console.log(': ', event);
      console.log('Blobs: ', recordedBlobs);
    };

    //
    mediaRecorder.ondataavailable = (event) => {
      console.log('handleDataAvailable', event);
      //
      if (event.data && event.data.size > 0) {
        //
        recordedBlobs.push(event.data);
      }
    };
    //10
    mediaRecorder.start(10);
    console.log('MediaRecorder started', mediaRecorder);
  }

  stopRecord = (e) => {
    //
    mediaRecorder.stop();
    //
    stream.getTracks().forEach(track => track.stop());
    //stream
    stream = null;

    //blob,video/webm
    const blob = new Blob(recordedBlobs, { type: 'video/webm' });
    //
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.style.display = 'none';
    a.href = url;
    //
    a.download = 'screen.webm';
    //a
    document.body.appendChild(a);
    a.click();
    setTimeout(() => {
      document.body.removeChild(a);
      //url.
      window.URL.revokeObjectURL(url);
    }, 100);
  }

  render() {
    return (
      <div className="container">
        <h1>
          <span></span>
        </h1>
        {/*  */}
        <video className="video" ref="myVideo" autoPlay playsInline></video>
        <Button onClick={this.startCaptureScreen} style={{ marginRight: "10px" }}></Button>
        <Button onClick={this.stopRecord}></Button>
      </div>
    );
  }
}
//
export default RecordScreen;
