import React from "react";
import { Button } from "antd";
import '../styles/css/datachannel.scss';

//
let localConnection;
//
let remoteConnection;
//
let sendChannel;
//
let receiveChannel;
/**
 * 
 */
class DataChannel extends React.Component {

  //
  call = async () => {
    console.log('...');

    //ICE Server,Google
    let configuration = { "iceServers": [{ "url": "stun:stun.l.google.com:19302" }] };

    //RTCPeerConnection
    localConnection = new RTCPeerConnection(configuration);
    console.log('PeerConnection:localConnection');
    //Candidate
    localConnection.addEventListener('icecandidate', this.onLocalIceCandidate);

    //
    sendChannel = localConnection.createDataChannel('webrtc-datachannel');
    //onopen
    sendChannel.onopen = this.onSendChannelStateChange;
    //onclose
    sendChannel.onclose = this.onSendChannelStateChange;

    //RTCPeerConnection
    remoteConnection = new RTCPeerConnection(configuration);
    console.log('PeerConnection:remoteConnection');
    //Candidate
    remoteConnection.addEventListener('icecandidate', this.onRemoteIceCandidate);

    //
    remoteConnection.ondatachannel = this.receiveChannelCallback;

    //ICE
    localConnection.addEventListener('iceconnectionstatechange', this.onLocalIceStateChange);
    //ICE
    remoteConnection.addEventListener('iceconnectionstatechange', this.onRemoteIceStateChange);

    try {
      console.log('localConnectionOffer');
      //Offer
      const offer = await localConnection.createOffer();
      //Offer
      await this.onCreateOfferSuccess(offer);
    } catch (e) {
      //Offer
      this.onCreateSessionDescriptionError(e);
    }
  }

  //
  onCreateSessionDescriptionError = (error) => {
    console.log(`SD: ${error.toString()}`);
  }

  //Offer
  onCreateOfferSuccess = async (desc) => {
    //localConnectionOfferSDP
    console.log(`localConnectionOfferSDP\n${desc.sdp}`);
    console.log('localConnectionstart');
    try {
      //localConnection
      await localConnection.setLocalDescription(desc);
      this.onSetLocalSuccess(localConnection);
    } catch (e) {
      this.onSetSessionDescriptionError();
    }

    console.log('remoteConnection');
    try {
      //remoteConnection
      await remoteConnection.setRemoteDescription(desc);
      this.onSetRemoteSuccess(remoteConnection);
    } catch (e) {
      //
      this.onSetSessionDescriptionError();
    }

    console.log('remoteConnectionAnswer');
    try {
      //Answer
      const answer = await remoteConnection.createAnswer();
      //
      await this.onCreateAnswerSuccess(answer);
    } catch (e) {
      //
      this.onCreateSessionDescriptionError(e);
    }
  }

  //
  onSetLocalSuccess = (pc) => {
    console.log(`${this.getName(pc)}:setLocalDescription`);
  }

  //
  onSetRemoteSuccess = (pc) => {
    console.log(`${this.getName(pc)}:setRemoteDescription`);
  }

  //SD
  onSetSessionDescriptionError = (error) => {
    console.log(`SD: ${error.toString()}`);
  }

  getName = (pc) => {
    return (pc === localConnection) ? 'localConnection' : 'remoteConnection';
  }

  //
  onCreateAnswerSuccess = async (desc) => {
    //SDP
    console.log(`remoteConnectionAnswer:\n${desc.sdp}`);
    console.log('remoteConnection:setLocalDescription');
    try {
      //remoteConnection
      await remoteConnection.setLocalDescription(desc);
      this.onSetLocalSuccess(remoteConnection);
    } catch (e) {
      this.onSetSessionDescriptionError(e);
    }
    console.log('localConnection:setRemoteDescription');
    try {
      //localConnection,remoteConnection
      await localConnection.setRemoteDescription(desc);
      this.onSetRemoteSuccess(localConnection);
    } catch (e) {
      this.onSetSessionDescriptionError(e);
    }
  }

  //Candidate
  onLocalIceCandidate = async (event) => {
    try {
      if (event.candidate) {
        //localConnectionCandidateremoteConnection
        await remoteConnection.addIceCandidate(event.candidate);
        this.onAddIceCandidateSuccess(remoteConnection);
      }
    } catch (e) {
      this.onAddIceCandidateError(remoteConnection, e);
    }
    console.log(`IceCandidate:\n${event.candidate ? event.candidate.candidate : '(null)'}`);
  }

  //Candidate
  onRemoteIceCandidate = async (event) => {
    try {
      if (event.candidate) {
        //remoteConnectionCandidatelocalConnection
        await localConnection.addIceCandidate(event.candidate);
        this.onAddIceCandidateSuccess(localConnection);
      }
    } catch (e) {
      this.onAddIceCandidateError(localConnection, e);
    }
    console.log(`IceCandidate:\n${event.candidate ? event.candidate.candidate : '(null)'}`);
  }

  //Candidate
  onAddIceCandidateSuccess = (pc) => {
    console.log(`${this.getName(pc)}IceCandidate`);
  }

  //Candidate
  onAddIceCandidateError = (pc, error) => {
    console.log(`${this.getName(pc)}IceCandidate: ${error.toString()}`);
  }

  //ICE
  onLocalIceStateChange = (event) => {
    console.log(`localConnectionICE: ${localConnection.iceConnectionState}`);
    console.log('ICE: ', event);
  }

  //ICE
  onRemoteIceStateChange = (event) => {
    console.log(`remoteConnectionICE: ${remoteConnection.iceConnectionState}`);
    console.log('ICE: ', event);
  }

  //
  hangup = () => {
    console.log('');
    //localConnection
    localConnection.close();
    //remoteConnection
    remoteConnection.close();
    //localConnection
    localConnection = null;
    //remoteConnection
    remoteConnection = null;
  }

  sendData = () => {
    let dataChannelSend = this.refs['dataChannelSend'];
    const data = dataChannelSend.value;
    sendChannel.send(data);
    console.log(':' + data);
  }

  //
  receiveChannelCallback = (event) => {
    console.log('Receive Channel Callback');
    //
    receiveChannel = event.channel;
    //
    receiveChannel.onmessage = this.onReceiveMessageCallback;
    //onopen
    receiveChannel.onopen = this.onReceiveChannelStateChange;
    //onclose
    receiveChannel.onclose = this.onReceiveChannelStateChange;
  }
  
  //
  onReceiveMessageCallback = (event) => {
    console.log(':' + event.data);
    let dataChannelReceive = this.refs['dataChannelReceive'];
    dataChannelReceive.value = event.data;
  }
  
  //
  onSendChannelStateChange = () => {
    const readyState = sendChannel.readyState;
    console.log(': ' + readyState);
  }
  
  //
  onReceiveChannelStateChange = () => {
    const readyState = receiveChannel.readyState;
    console.log(':' + readyState);
  }

  render() {
    return (
      <div className="container">
        <div>
          <div>
            <h2></h2>
            <textarea ref="dataChannelSend" disabled={false}
              placeholder="..." />
          </div>
          <div>
            <h2></h2>
            <textarea ref="dataChannelReceive" disabled={false} />
          </div>
        </div>
        <div>
          <Button onClick={this.call} style={{ marginRight: "10px" }}></Button>
          <Button onClick={this.sendData} style={{ marginRight: "10px" }}></Button>
          <Button onClick={this.hangup} style={{ marginRight: "10px" }}></Button>
        </div>
      </div>
    );
  }
}
//
export default DataChannel;
