import React from "react";
import SoundMeter from './soundmeter';

//
let soundMeter;
/**
 * 
 */
class AudioVolume extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      //
      audioLevel: 0,
    }
  }

  componentDidMount() {

    try {
      //AudioContext
      window.AudioContext = window.AudioContext || window.webkitAudioContext;
      //AudioContext
      window.audioContext = new AudioContext();
    } catch (e) {
      console.log('API.');
    }
    
    //SoundMeter,
    soundMeter = window.soundMeter = new SoundMeter(window.audioContext);

    const constraints = window.constraints = {
      //
      audio: true,
      //
      video: false
    };
    //
    navigator.mediaDevices.getUserMedia(constraints).then(this.handleSuccess).catch(this.handleError);
  }

  //
  handleSuccess = (stream) => {
    window.stream = stream;
    //
    soundMeter.connectToSource(stream);
    //
    setTimeout(this.soundMeterProcess, 100);
  }

  //
  soundMeterProcess = () => {
    //,,
    var val = (window.soundMeter.instant.toFixed(2) * 348) + 1;
    //
    this.setState({ audioLevel: val });
    //100soundMeterProcess,
    setTimeout(this.soundMeterProcess, 100);
  }

  //
  handleError(error) {
    console.log('navigator.MediaDevices.getUserMedia error: ', error.message, error.name);
  }

  render() {
    return (
      <div className="container">
        <h1>
          <span></span>
        </h1>
        {/* div,, */}
        <div style={{
          width: this.state.audioLevel + 'px',
          height: '10px',
          backgroundColor: '#8dc63f',
          marginTop: '20px',
        }}>
        </div>
      </div>
    );
  }
}
//
export default AudioVolume;
