import React from "react";
import { Button, message } from "antd";

/**
 * 
 */
class ScreenSharing extends React.Component {

  //
  startScreenShare = async (e) => {
    try {
      //getDisplayMedia,{video:true}
      const stream = await navigator.mediaDevices.getDisplayMedia({video: true});
      console.log('handleSuccess:');
      this.handleSuccess(stream);
    } catch (e) {
      this.handleError(e);
    }
  }

  //,
  handleSuccess = (stream) => {
    const video = this.refs['myVideo'];
    //
    const videoTracks = stream.getVideoTracks();
    //
    console.log(`: ${videoTracks[0].label}`);
    window.stream = stream; 
    //stream
    video.srcObject = stream;
  }

  //
  handleError(error) {
    if (error.name === 'ConstraintNotSatisfiedError') {
      const v = constraints.video;
      //
      message.error(`:${v.width.exact} :${v.height.exact} `);
    } else if (error.name === 'PermissionDeniedError') {
      message.error(',');
    }
    message.error(`getUserMedia: ${error.name}`, error);
  }

  render() {
    return (
      <div className="container">
        <h1>
          <span></span>
        </h1>
        {/*  */}
        <video className="video" ref="myVideo" autoPlay playsInline></video>
        <Button onClick={this.startScreenShare}></Button>
      </div>
    );
  }
}
//
export default ScreenSharing;
