import React from "react";
import { Button } from "antd";
import '../styles/css/record-canvas.scss';

//
let mediaRecorder;
//
let recordedBlobs;
//
let stream;
//
let canvas;
//2D
let context;

/**
 * Canvas
 */
class RecordCanvas extends React.Component {

    componentDidMount() {
        this.drawLine();
    }

    drawLine = () => {
        //Canvas
        canvas = this.refs['canvas'];
        //Canvas2d
        context = canvas.getContext("2d");

        //
        context.fillStyle = '#CCC';
        //Canvas
        context.fillRect(0,0,320,240);

        context.lineWidth = 1;
        //
        context.strokeStyle = "#FF0000";

        // 
        canvas.addEventListener("mousedown", this.startAction);
        // 
        canvas.addEventListener("mouseup", this.endAction);
    }

    //
    startAction = (event) => {
        //
        context.beginPath();
        //
        context.moveTo(event.offsetX, event.offsetY);
        //
        context.stroke();
        //  
        canvas.addEventListener("mousemove", this.moveAction);
    }

    //  
    moveAction = (event) => {
        //
        context.lineTo(event.offsetX, event.offsetY);
        //
        context.stroke();
    }

    // 
    endAction = () => {
        //
        canvas.removeEventListener("mousemove", this.moveAction);
    }

    //Canvas
    startCaptureCanvas = async (e) => {
        stream = canvas.captureStream(10);
        const video = this.refs['video'];
        //
        const videoTracks = stream.getVideoTracks();
        //
        console.log(`: ${videoTracks[0].label}`);
        window.stream = stream;
        //stream
        video.srcObject = stream;

        //
        this.startRecord();
    }

    //
    startRecord = (e) => {
        //
        recordedBlobs = [];
        try {
            //MediaRecorder,
            mediaRecorder = new MediaRecorder(window.stream, { mimeType: 'video/webm' });
        } catch (e) {
            console.error('MediaRecorder:', e);
            return;
        }

        //
        mediaRecorder.onstop = (event) => {
            console.log(': ', event);
            console.log('Blobs: ', recordedBlobs);
        };

        //
        mediaRecorder.ondataavailable = (event) => {
            console.log('handleDataAvailable', event);
            //
            if (event.data && event.data.size > 0) {
                //
                recordedBlobs.push(event.data);
            }
        };
        //10
        mediaRecorder.start(100);
        console.log('MediaRecorder started', mediaRecorder);
    }

    stopRecord = (e) => {
        //
        mediaRecorder.stop();
        //
        stream.getTracks().forEach(track => track.stop());
        //stream
        stream = null;

        //blob,video/webm
        const blob = new Blob(recordedBlobs, { type: 'video/webm' });
        //
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.style.display = 'none';
        a.href = url;
        //
        a.download = 'canvas.webm';
        //a
        document.body.appendChild(a);
        a.click();
        setTimeout(() => {
            document.body.removeChild(a);
            //url.
            window.URL.revokeObjectURL(url);
        }, 100);
    }

    render() {
        return (
            <div className="container">
                <h1>
                    <span>Canvas</span>
                </h1>
                <div>
                    {/* Canvas */}
                    <div className="small-canvas">
                        {/* Canvas */}
                        <canvas ref='canvas'></canvas>
                    </div>
                    <video className="small-video" ref='video' playsInline autoPlay></video>
                </div>
                <Button className="button" onClick={this.startCaptureCanvas}></Button>
                <Button className="button" onClick={this.stopRecord}></Button>
            </div>
        );
    }
}
//
export default RecordCanvas;
