import React from "react";
import { Button, Select } from "antd";

const { Option } = Select;

//QVGA 320*240
const qvgaConstraints = {
    video: { width: { exact: 320 }, height: { exact: 240 } }
};

//VGA 640*480
const vgaConstraints = {
    video: { width: { exact: 640 }, height: { exact: 480 } }
};

// 1280*720
const hdConstraints = {
    video: { width: { exact: 1280 }, height: { exact: 720 } }
};

// 1920*1080
const fullHdConstraints = {
    video: { width: { exact: 1920 }, height: { exact: 1080 } }
};

//2K 2560*1440
const twoKConstraints = {
    video: { width: { exact: 2560 }, height: { exact: 1440 } }
};

//4K 4096*2160
const fourKConstraints = {
    video: { width: { exact: 4096 }, height: { exact: 2160 } }
};

//8K 7680*4320
const eightKConstraints = {
    video: { width: { exact: 7680 }, height: { exact: 4320 } }
};

//
let stream;
//
let video;

/**
 * 
 */
class Resolution extends React.Component {

    componentDidMount() {
        //video
        video = this.refs['video'];
    }

    //
    getMedia = (constraints) => {
        //
        if (stream) {
            //
            stream.getTracks().forEach(track => {
                track.stop();
            });
        }
        //
        navigator.mediaDevices.getUserMedia(constraints)
            //
            .then(this.gotStream)
            //
            .catch(e => {
                this.handleError(e);
            });
    }

    //
    gotStream = (mediaStream) => {
        stream = window.stream = mediaStream;
        //videomediaStream
        video.srcObject = mediaStream;
        const track = mediaStream.getVideoTracks()[0];
        const constraints = track.getConstraints();
        console.log(':' + JSON.stringify(constraints));
    }

    //
    handleError(error) {
        console.log(`getUserMedia: ${error.name}`, error);
    }

    //
    handleChange = (value) => {
        console.log(`selected ${value}`);
        //
        switch (value) {
            case 'qvga':
                this.getMedia(qvgaConstraints);
                break;
            case 'vga':
                this.getMedia(vgaConstraints);
                break;
            case 'hd':
                this.getMedia(hdConstraints);
                break;
            case 'fullhd':
                this.getMedia(fullHdConstraints);
                break;
            case '2k':
                this.getMedia(twoKConstraints);
                break;
            case '4k':
                this.getMedia(fourKConstraints);
                break;
            case '8k':
                this.getMedia(eightKConstraints);
                break;
            default:
                this.getMedia(vgaConstraints);
                break;
        }
    }

    //
    dynamicChange = (e) => {
        //
        const track = window.stream.getVideoTracks()[0];
        //
        console.log(':' + JSON.stringify(hdConstraints));
        track.applyConstraints(constraints)
            .then(() => {
              console.log('...');
            })
            .catch(err => {
              console.log(':', err.name);
            });
      }

    render() {
        return (
            <div className="container">
                <h1>
                    <span></span>
                </h1>
                {/*  */}
                <video ref='video' playsInline autoPlay></video>
                {/*  */}
                <Select defaultValue="vga" style={{ width: '100px',marginLeft:'20px' }} onChange={this.handleChange}>
                    <Option value="qvga">QVGA</Option>
                    <Option value="vga">VGA</Option>
                    <Option value="hd"></Option>
                    <Option value="fullhd"></Option>
                    <Option value="2k">2K</Option>
                    <Option value="4k">4K</Option>
                    <Option value="8k">8K</Option>
                </Select>
                <Button onClick={this.dynamicChange} style={{ marginLeft:'20px' }}></Button>
            </div>
        );
    }
}
//
export default Resolution;
