import React from "react";
import { Button } from "antd";

//
let localVideo;
//
let remoteVideo;
//
let localStream;
//PeerA
let peerConnA;
//PeerB
let peerConnB;
/**
 * Video
 */
class PeerConnectionVideo extends React.Component {

    //
    canPlay = () => {
        //
        const fps = 0;
        //,
        localStream = localVideo.captureStream(fps);
    }

    componentDidMount() {
        //
        localVideo = this.refs['localVideo'];
        //
        remoteVideo = this.refs['remoteVideo'];
      }
    
      //
      call = async () => {
        console.log('...');
        //
        const videoTracks = localStream.getVideoTracks();
        //
        const audioTracks = localStream.getAudioTracks();
        //
        if (videoTracks.length > 0) {
          //
          console.log(`: ${videoTracks[0].label}`);
        }
        //
        if (audioTracks.length > 0) {
          //
          console.log(`: ${audioTracks[0].label}`);
        }
    
        //ICE Server,Google
        let configuration = { "iceServers": [{ "url": "stun:stun.l.google.com:19302" }] };
    
        //RTCPeerConnection
        peerConnA = new RTCPeerConnection(configuration);
        console.log('PeerConnection:peerConnA');
        //Candidate
        peerConnA.addEventListener('icecandidate', this.onIceCandidateA);
    
        //RTCPeerConnection
        peerConnB = new RTCPeerConnection(configuration);
        console.log('PeerConnection:peerConnB');
        //Candidate
        peerConnB.addEventListener('icecandidate',  this.onIceCandidateB);
    
        //ICE
        peerConnA.addEventListener('iceconnectionstatechange', this.onIceStateChangeA);
        //ICE
        peerConnB.addEventListener('iceconnectionstatechange', this.onIceStateChangeB);
    
        //track,
        peerConnB.addEventListener('track', this.gotRemoteStream);
    
        //peerConnA.addStream(localStream);
        //
        localStream.getTracks().forEach((track) => {
          //
          peerConnA.addTrack(track, localStream);
        });
        console.log('peerConnA');
    
        try {
          console.log('peerConnAOffer');
          //Offer
          const offer = await peerConnA.createOffer();
          //Offer
          await this.onCreateOfferSuccess(offer);
        } catch (e) {
          //Offer
          this.onCreateSessionDescriptionError(e);
        }
      }
    
      //
      onCreateSessionDescriptionError = (error) => {
        console.log(`SD: ${error.toString()}`);
      }
    
      //Offer
      onCreateOfferSuccess = async (desc) => {
        //peerConnAOfferSDP
        console.log(`peerConnAOfferSDP\n${desc.sdp}`);
        console.log('peerConnAstart');
        try {
          //peerConnA
          await peerConnA.setLocalDescription(desc);
          this.onSetLocalSuccess(peerConnA);
        } catch (e) {
          this.onSetSessionDescriptionError();
        }
    
        console.log('peerConnB');
        try {
          //peerConnB
          await peerConnB.setRemoteDescription(desc);
          this.onSetRemoteSuccess(peerConnB);
        } catch (e) {
          //
          this.onSetSessionDescriptionError();
        }
    
        console.log('peerConnBAnswer');
        try {
          //Answer
          const answer = await peerConnB.createAnswer();
          //
          await this.onCreateAnswerSuccess(answer);
        } catch (e) {
          //
          this.onCreateSessionDescriptionError(e);
        }
      }
    
      //
      onSetLocalSuccess = (pc) => {
        console.log(`${this.getName(pc)}:setLocalDescription`);
      }
    
      //
      onSetRemoteSuccess = (pc) => {
        console.log(`${this.getName(pc)}:setRemoteDescription`);
      }
    
      //SD
      onSetSessionDescriptionError = (error) => {
        console.log(`SD: ${error.toString()}`);
      }
    
      getName = (pc) => {
        return (pc === peerConnA) ? 'peerConnA' : 'peerConnB';
      }
    
      //
      gotRemoteStream = (e) => {
        if (remoteVideo.srcObject !== e.streams[0]) {
          //
          remoteVideo.srcObject = e.streams[0];
          console.log('peerConnB');
        }
      }
    
      //
      onCreateAnswerSuccess = async (desc) => {
        //SDP
        console.log(`peerConnBAnswer:\n${desc.sdp}`);
        console.log('peerConnB:setLocalDescription');
        try {
          //peerConnB
          await peerConnB.setLocalDescription(desc);
          this.onSetLocalSuccess(peerConnB);
        } catch (e) {
          this.onSetSessionDescriptionError(e);
        }
        console.log('peerConnA:setRemoteDescription');
        try {
          //peerConnA,peerConnB
          await peerConnA.setRemoteDescription(desc);
          this.onSetRemoteSuccess(peerConnA);
        } catch (e) {
          this.onSetSessionDescriptionError(e);
        }
      }
    
      //Candidate
      onIceCandidateA = async (event) => {
        try {
          if(event.candidate){
            //peerConnACandidatepeerConnB
            await peerConnB.addIceCandidate(event.candidate);
            this.onAddIceCandidateSuccess(peerConnB);
          }
        } catch (e) {
          this.onAddIceCandidateError(peerConnB, e);
        }
        console.log(`IceCandidate:\n${event.candidate ? event.candidate.candidate : '(null)'}`);
      }
    
      //Candidate
      onIceCandidateB = async (event) => {
        try {
          if(event.candidate){
            //peerConnBCandidatepeerConnA
            await peerConnA.addIceCandidate(event.candidate);
            this.onAddIceCandidateSuccess(peerConnA);
          }
        } catch (e) {
          this.onAddIceCandidateError(peerConnA, e);
        }
        console.log(`IceCandidate:\n${event.candidate ? event.candidate.candidate : '(null)'}`);
      }
    
      //Candidate
      onAddIceCandidateSuccess = (pc) => {
        console.log(`${this.getName(pc)}IceCandidate`);
      } 
    
      //Candidate
      onAddIceCandidateError = (pc, error) => {
        console.log(`${this.getName(pc)}IceCandidate: ${error.toString()}`);
      }
    
      //ICE
      onIceStateChangeA = (event) => {
        console.log(`peerConnAICE: ${peerConnA.iceConnectionState}`);
        console.log('ICE: ', event);
      }
    
      //ICE
      onIceStateChangeB = (event) => {
        console.log(`peerConnBICE: ${peerConnB.iceConnectionState}`);
        console.log('ICE: ', event);
      }
    
      //
      hangup = () => {
        console.log('');
        //peerConnA
        peerConnA.close();
        //peerConnB
        peerConnB.close();
        //peerConnA
        peerConnA = null;
        //peerConnB
        peerConnB = null;
      }

    render() {
        return (
            <div className="container">
                <h1>
                    <span>Video</span>
                </h1>
                <video ref="localVideo" playsInline controls loop muted onCanPlay={this.canPlay}>
                    {/* mp4 */}
                    <source src="./assets/webrtc.mp4" type="video/mp4" />
                </video>
                {/*  */}
                <video ref="remoteVideo" playsInline autoPlay></video>
                <div>
                <Button ref="callButton" onClick={this.call} style={{marginRight:"10px"}}></Button>
                <Button ref="hangupButton" onClick={this.hangup} style={{marginRight:"10px"}}></Button>
                </div>
            </div>
        );
    }
}
//
export default PeerConnectionVideo;
