import React from "react";
import { Button, } from "antd";
import "../styles/css/record-video.scss";

//
let mediaRecorder;
//
let recordedBlobs;
//,
let videoPreview;
//,
let videoPlayer;

/**
 * 
 */
class RecordVideo extends React.Component {
  constructor() {
    super();
    //
    this.state = {
      status: 'start',
    }
  }

  componentDidMount() {
    //
    videoPreview = this.refs['videoPreview'];
    //
    videoPlayer = this.refs['videoPlayer'];
  }

  //
  startClickHandler = async (e) => {
    //
    let constraints = {
      //
      audio: true,
      //1280*720
      video: {
        width: 1280, height: 720
      }
    };
    console.log(':', constraints);
    try {
      //
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      window.stream = stream;
      //stream
      videoPreview.srcObject = stream;
      this.setState({
        status: 'startRecord',
      });
    } catch (e) {
      console.error('navigator.getUserMedia error:', e);
    }
  }

  //
  startRecordButtonClickHandler = (e) => {
    //
    recordedBlobs = [];
    //mimeType,vp9 vp8
    let options = { mimeType: 'video/webm;codecs=vp9' };
    if (!MediaRecorder.isTypeSupported(options.mimeType)) {
      console.error("video/webm;codecs=vp9");
      options = { mimeType: 'video/webm;codecs=vp8' };
      if (!MediaRecorder.isTypeSupported(options.mimeType)) {
        console.error("video/webm;codecs=vp8");
        options = { mimeType: 'video/webm' };
        if (!MediaRecorder.isTypeSupported(options.mimeType)) {
          console.error(`video/webm`);
          options = { mimeType: '' };
        }
      }
    }

    try {
      //MediaRecorder,
      mediaRecorder = new MediaRecorder(window.stream, options);
    } catch (e) {
      console.error('MediaRecorder:', e);
      return;
    }

    //
    mediaRecorder.onstop = (event) => {
      console.log(': ', event);
      console.log('Blobs: ', recordedBlobs);
    };
    mediaRecorder.ondataavailable = this.handleDataAvailable;
    //10
    mediaRecorder.start(10);
    console.log('MediaRecorder started', mediaRecorder);
    //
    this.setState({
      status: 'stopRecord',
    });
  }

  stopRecordButtonClickHandler = (e) => {
    //
    mediaRecorder.stop();
    //
    this.setState({
      status: 'play',
    });
  }

  //
  playButtonClickHandler = (e) => {
     //blob,video/webm
    const blob = new Blob(recordedBlobs, { type: 'video/webm' });
    videoPlayer.src = null;
    videoPlayer.srcObject = null;
    //URL.createObjectURL()URL
    videoPlayer.src = window.URL.createObjectURL(blob);
    //
    videoPlayer.controls = true;
    //
    videoPlayer.play();
    //
    this.setState({
      status: 'download',
    });
  }

  //
  downloadButtonClickHandler = (e) => {
    //blob,video/webm
    const blob = new Blob(recordedBlobs, { type: 'video/webm' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.style.display = 'none';
    a.href = url;
    //
    a.download = 'test.webm';
    //a
    document.body.appendChild(a);
    a.click();
    setTimeout(() => {
      document.body.removeChild(a);
      //URL.revokeObjectURL()URL.createObjectURL()URL.
      window.URL.revokeObjectURL(url);
    }, 100);
    //
    this.setState({
      status: 'start',
    });
  }

  //
  handleDataAvailable = (event) => {
    console.log('handleDataAvailable', event);
    //
    if (event.data && event.data.size > 0) {
      //
      recordedBlobs.push(event.data);
    }
  }

  render() {

    return (
      <div className="container">

        <h1>
          <span></span>
        </h1>
        {/*  muted */}
        <video className="small-video" ref="videoPreview" playsInline autoPlay muted></video>
         {/*  loop */}
        <video className="small-video" ref="videoPlayer" playsInline loop></video>

        <div>
          <Button
            className="button"
            onClick={this.startClickHandler}
            disabled={this.state.status != 'start'}>
            
            </Button>
          <Button
            className="button"
            disabled={this.state.status != 'startRecord'}
            onClick={this.startRecordButtonClickHandler}>
            
          </Button>
          <Button
            className="button"
            disabled={this.state.status != 'stopRecord'}
            onClick={this.stopRecordButtonClickHandler}>
            
          </Button>
          <Button
            className="button"
            disabled={this.state.status != 'play'}
            onClick={this.playButtonClickHandler}>
            
          </Button>
          <Button
            className="button"
            disabled={this.state.status != 'download'}
            onClick={this.downloadButtonClickHandler}>
            
            </Button>
        </div>
      </div>
    );
  }
}
//
export default RecordVideo;
